import React, { useState, useEffect } from 'react';
import * as SunCalc from 'suncalc';
import './App.css';

function App() {
  const [location, setLocation] = useState(null);
  const [sunsetData, setSunsetData] = useState(null);
  const [deviceOrientation, setDeviceOrientation] = useState(0);
  const [error, setError] = useState(null);
  const [hasVibrated, setHasVibrated] = useState(false);
  const [isAligned, setIsAligned] = useState(false);

  const ALIGNMENT_TOLERANCE = 10;

  // 1. Obter geolocaliza√ß√£o
  useEffect(() => {
    const getLocation = () => {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            setLocation({
              lat: position.coords.latitude,
              lng: position.coords.longitude
            });
          },
          () => setError("Ative a localiza√ß√£o para usar o app")
        );
      } else {
        setError("Geolocaliza√ß√£o n√£o suportada");
      }
    };
    getLocation();
  }, []);

  // 2. Calcular dire√ß√£o do p√¥r do sol
  useEffect(() => {
    if (location) {
      const now = new Date();
      const times = SunCalc.getTimes(now, location.lat, location.lng);
      const sunsetPos = SunCalc.getPosition(times.sunset, location.lat, location.lng);
      const azimuth = (sunsetPos.azimuth * 180 / Math.PI + 180) % 360;

      setSunsetData({
        time: times.sunset.toLocaleTimeString(),
        azimuth
      });
    }
  }, [location]);

  // 3. Configurar sensor de orienta√ß√£o
  useEffect(() => {
    const handleOrientation = (e) => {
      if (e.alpha !== null) {
        setDeviceOrientation(360 - e.alpha);
      }
    };

    window.addEventListener('deviceorientation', handleOrientation, true);
    return () => window.removeEventListener('deviceorientation', handleOrientation);
  }, []);

  // 4. Calcular √¢ngulo relativo
  const calculateRelativeAngle = () => {
    if (!sunsetData) return 0;

    const angle = (sunsetData.azimuth - deviceOrientation + 360) % 360;
    const delta = Math.min(Math.abs(angle), 360 - Math.abs(angle));
    const aligned = delta < ALIGNMENT_TOLERANCE;

    setIsAligned(aligned);

    if (aligned && !hasVibrated) {
      if (navigator.vibrate) navigator.vibrate(200);
      setHasVibrated(true);
    } else if (!aligned && hasVibrated) {
      setHasVibrated(false);
    }

    return angle;
  };

  const relativeAngle = calculateRelativeAngle();

  return (
    <div className="App">
      <header className="App-header">
        <h1>B√∫ssola do P√¥r do Sol</h1>

        {error && <p className="error">{error}</p>}

        {sunsetData && (
          <div className="compass-container">
            <div className={`compass ${isAligned ? 'aligned' : ''}`}>
              <div 
                className="arrow" 
                style={{ transform: `rotate(${relativeAngle}deg)` }}
              >
                <div className="sun-icon">‚òÄÔ∏è</div>
              </div>
              <div className="compass-circle"></div>
            </div>

            <div className="info">
              <p>Hor√°rio: {sunsetData.time}</p>
              <p>Dire√ß√£o: {sunsetData.azimuth.toFixed(1)}¬∞</p>
              {isAligned && <p className="aligned-message">üì∏ Perfeito! Tire sua foto do p√¥r do sol!</p>}
              {!isAligned && <p className="help">Gire o celular para alinhar a b√∫ssola</p>}
            </div>
          </div>
        )}
      </header>
    </div>
  );
}

export default App;
